'use client';

import Image from 'next/image';
import Link from 'next/link';
import {
  createTheme,
  ThemeProvider,
  Navbar,
  NavbarBrand,
  NavbarToggle,
  NavbarCollapse,
  NavbarLink,
} from 'flowbite-react';

import { navTheme } from '@/theme/navbarTheme';
/* ---------------------------------------------------------- */

const MainNavbar = () => (
  <>
    {/* แถบแดงเต็มจอด้านบน */}
    <div className="w-full h-[6px] bg-[#ef4653]" />

    <ThemeProvider theme={{ navbar: navTheme }}>
      <Navbar fluid rounded={false}>
        <NavbarBrand as={Link} href="/">
          <Image
            src="/logomain.png"
            alt="ntivity logo"
            width={120}
            height={32}
            priority
          />
        </NavbarBrand>

        <NavbarToggle />

        <NavbarCollapse className="gap-6">
          <NavbarLink href="/" as={Link} active>
            หน้าหลัก
          </NavbarLink>

          <NavbarLink href="/me/activities" as={Link}>
            กิจกรรมของฉัน
          </NavbarLink>

          <NavbarLink href="/registration" as={Link}>
            ลงทะเบียนกิจกรรม
          </NavbarLink>
        </NavbarCollapse>
      </Navbar>
    </ThemeProvider>
  </>
);

export default MainNavbar;
